import React, { Component } from 'react';
import './App.css';
import ReactAplayer from './react-aplayer';
import appData from './data';
import Card from './card'
import APlayer from 'aplayer';

class App extends Component {

  swtch=(d)=>{
    ReactAplayer.list.switch(d);
  }

  render() {
   
    return (
      <div className="landing">
        
             <div className="aplayer-wrap">        
          <ReactAplayer
            {...appData.apLrcList}
            onInit={this.initAp1}
            onPlay={this.onPlay}
            onPause={this.onPause}
          />
             </div>
<div className='ml3 center w-90' >
{appData.apLrcList.audio.map((data,i)=>{
  return(
      <Card title={data.name} imgurl={data.cover} artist={data.artist} url={data.url} onClick={()=>{this.swtch(2)}}/>  
)
})
}

</div>
   <ReactAplayer {...appData.apFixedLrcList} />


      </div>
    );
  }
}

export default App;
